.ant-layout-header {
  background: #fff !important;
  padding: 0px !important;
}

.ant-layout-header .ant-menu-root{
  background: rgb(10, 64, 126) !important;
}

.site-layout-content {
  min-height: 280px;
  padding: 24px;
  background: #fff;
}

.logo {
  float: left;
  width: 120px;
  height: 31px;
  margin: 16px 24px 16px 0;
  background: rgba(255, 255, 255, 0.3);
}

.ant-row-rtl .logo {
  float: right;
  margin: 16px 0 16px 24px;
}
